@font-face {
  font-family: 'didot';
  src: url("../assets/GFS_Didot/GFSDidot-Regular.ttf") format("truetype"); }

html,
body {
  overflow-x: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: didot; }

a {
  text-decoration: none; }

p {
  margin-bottom: 24px; }

ul {
  list-style-type: none; }

li {
  list-style-type: none; }

footer {
  background-color: black;
  color: white; }

.font48 {
  font-size: 48px; }

.font-dark-gray {
  color: #858585 !important; }

.font-red {
  color: #c50000; }

.text-right {
  text-align: right; }

.h-90vh {
  height: 90vh; }

.h-100vh {
  height: 100vh; }

.w-100vw {
  width: 100vw; }

.maxW1024 {
  max-width: 1024px; }

.max-w-860 {
  max-width: 860px; }

.max-w-920 {
  max-width: 920px; }

.max-w-1024 {
  max-width: 1024px; }

.didot {
  font-family: didot; }

.didot-bold {
  font-family: didot;
  font-weight: 600; }

.pl-5 {
  padding-left: 1.25rem !important; }

.pl-4 {
  padding-left: 1rem !important; }

.pl-3 {
  padding-left: 0.75rem !important; }

.pl-2 {
  padding-left: 0.5rem !important; }

.pl-1 {
  padding-left: 0.25rem !important; }

.pr-5 {
  padding-right: 1.25rem !important; }

.pr-4 {
  padding-right: 1rem !important; }

.pr-3 {
  padding-right: 0.75rem !important; }

.pr-2 {
  padding-right: 0.5rem !important; }

.pr-1 {
  padding-right: 0.25rem !important; }

@media (max-width: 575px) {
  .d-xs-none {
    display: none; }
  .col-xs-12 {
    flex: 0 0 auto;
    width: 100%; }
  .text-xs-left {
    text-align: left !important; }
  .text-xs-center {
    text-align: center !important; }
  .px-xs-0 {
    padding-left: 0 !important;
    padding-right: 0 !important; }
  .p-xs-3 {
    padding: 0.75rem !important; }
  .p-xs-2 {
    padding: 0.5rem !important; }
  .p-xs-1 {
    padding: 0.25rem !important; }
  .p-xs-0 {
    padding: 0 !important; } }

@media (min-width: 576px) {
  .pl-sm-5 {
    padding-left: 1.25rem !important; }
  .pl-sm-4 {
    padding-left: 1rem !important; }
  .pl-sm-3 {
    padding-left: 0.75rem !important; }
  .pl-sm-2 {
    padding-left: 0.5rem !important; }
  .pl-sm-1 {
    padding-left: 0.25rem !important; }
  .pr-sm-5 {
    padding-right: 1.25rem !important; }
  .pr-sm-4 {
    padding-right: 1rem !important; }
  .pr-sm-3 {
    padding-right: 0.75rem !important; }
  .pr-sm-2 {
    padding-right: 0.5rem !important; }
  .pr-sm-1 {
    padding-right: 0.25rem !important; }
  .p-sm-5 {
    padding: 1.25rem !important; }
  .p-sm-4 {
    padding: 1rem !important; }
  .p-sm-3 {
    padding: 0.75rem !important; }
  .p-sm-2 {
    padding: 0.5rem !important; }
  .p-sm-1 {
    padding: 0.25rem !important; } }

footer .row {
  padding: 2.8125rem 1.25rem 0.5rem 1.25rem; }
  footer .row a {
    color: white;
    transition: ease 0.25s;
    line-height: 2; }
  footer .row a:hover {
    color: #c50000; }
  footer .row .col h4 {
    white-space: nowrap; }
  footer .row .col ul {
    padding: 0; }
